const app = getApp();
import helper from '../common/helper.js';
export default {
	data() {
		return {
			list:[],
			imgUrl: ''
		}
	},
	onLoad() {
		this.imgUrl = helper.imgUrl;
		this.getList()
	},
	
	methods: {
		// 获取数据
		getList(){
			uni.request({
				url: `${helper.requestUrl}/promote-teamlist`,
				method: 'GET',
				header: {
					authorization: app.globalData.token
				},
				success: res => {
					res = helper.null2str(res);
					console.log(res,'****');
					if (res.data.status_code == 200) {
						this.list = res.data.datas;
					} else {

					}
				}
			});
		},
		getMore(e){
			console.log(e)
			uni.navigateTo({
				url: `/pages/meTeamList?index=${e}`
			})
		},
		userDetail(i){
			console.log(i)
			
			let index = i.currentTarget.dataset.index
			let itemNew = i.currentTarget.dataset.item
			let level = ''
			if(itemNew.type == 'normal'){
				level = itemNew.deploy.userlevel.level_name
			}else{
				level = itemNew.deploy.vipuserlevel.level_name
			}
			let itemNewObj = {
				name: itemNew.name,
				mobile:itemNew.mobile,
				level_name: level,
				index: index,
				avatar: itemNew.avatar,
				created_at: itemNew.created_at,
				type:itemNew.type
			}
			let obj = JSON.stringify(itemNewObj)
			console.log(itemNew)
			uni.navigateTo({
				url:`/pages/teamPeopleDetail?itemDetail=${obj}`
			})
		}
	}
}
